
.image {
  background-position: center center;
  background-size: 100% 100%;
}
.building-card {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
}
.building-card > .header {
    height: 10px;
}
.building-card > .header.-set1 {
      background-color: #DF3FEE;
}
.building-card > .header.-set2 {
      background-color: #7F211B;
}
.building-card > .header.-set3 {
      background-color: #C70C13;
}
.building-card > .header.-set4 {
      background-color: #D7E60C;
}
.building-card > .header.-set5 {
      background-color: #181798;
}
.building-card > .header.-set6 {
      background-color: #F8A9FC;
}
.building-card > .header.-set7 {
      background-color: #35EBD4;
}
.building-card > .header.-set8 {
      background-color: #31FA1D;
}
.building-card > .body {
    position: relative;
    height: 90px;
}
.building-card > .body > .title {
      height: 20%;
      text-align: center;
}
.building-card > .body > .image {
      height: 60%;
}
.building-card > .body > .image img {
        width: 100%;
        height: 100%;
}
.building-card > .body > .price {
      height: 20%;
      text-align: center;
}



